import timeago from 'timeago.js'
/**
 * 
 * @method getTabInfo
 * @param  {string}   tab       
 * @param  {boolean}   good     
 * @param  {boolean}   top      
 * @param  {boolean}   isClass 
 * @return {String}             [description]
 */
export function getTabInfo (tab, good, top, isClass) {
  let className = ''
  let type = ''

  if (top) {
    type = ''
    className = 'top'
  } else if (good) {
    type = ''
    className = 'good'
  } else {
    switch (tab) {
      case 'top':
        type = ''
        className = 'top'
        break
      case 'good':
        type = ''
        className = 'good'
        break
      case 'share':
        type = ''
        className = 'share'
        break
      case 'ask':
        type = ''
        className = 'ask'
        break
      case 'job':
        type = ''
        className = 'job'
        break
      case 'all':
        type = ''
        className = 'all'
        break
      case 'dev':
        type = ''
        className = 'all'
        break
      default:
        type = ''
        className = 'none'
    }
  }

  return isClass ? className : type
}

/**
 * 
 * @method timeAgo
 * @param  {String} date 
 * @return {String}      
 */
function timeAgo (date) {
  let timeAgo = timeago()
  return timeAgo.format(date, 'zh_CN')
}

// 
function resolveDate (date) {

}

/**
 * 
 * @method formatDate
 * @param  {String}   date      
 * @param  {Boolean}  humanity  
 * @return {String}             
 */
export function formatDate (date, humanity) {
  if (humanity) {
    return timeAgo(date)
  } else {
    resolveDate(date)
  }
}

/**
 * 
 * @method throttle
 * @param  {Function} fn      
 * @param  {Number}   delay   
 * @param  {Number}   atleast 
 * @return {[type]}           [description]
 */
export function throttle (fn, delay, atleast) {
  let timer = null
  let startTime = new Date()

  return function () {
    let context = this
    let args = arguments
    let curTime = new Date()

    clearTimeout(timer)
    if (curTime - startTime >= atleast) {
      fn.apply(context, args)   // apply  (this)  
      startTime = curTime
    } else {
      timer = setTimeout(function () {
        fn.apply(context, args)
      }, delay)
    }
  }
}

/**
 * 
 * @method scrollToTop
 * @param  {DOM}    scrollDom 
 * @param  {Number}    scale     
 * @param  {Number}    time      
 * @return {Null}              
 */
export function scrollToTop (scrollDom, scale, time) {
  //  1/5 10
  let scrolling = setInterval(() => {
    let curScrollTop = scrollDom.scrollTop
    if (curScrollTop < 10) {
      curScrollTop *= scale
    }

    if (curScrollTop < 1) {
      clearInterval(scrolling)
    }

    scrollDom.scrollTop -= curScrollTop / scale
  }, time)
}



